* {
  margin: 0;
}
html, body {
  height: 100%;
}
a:focus{
  outline:none;
}

.header, .footer {
  box-sizing: border-box;
  width: 100%;
}

body {
  position: relative;
  padding-bottom: 58px;
  box-sizing: border-box;
}
.footer {
  position: absolute;
  bottom: 0;
  height: 58px;
}


.header {
  height: 100px;
  background: lavender;
  &_left {
    float: left;
    margin: 26px;
    width: 220px;
  }
  &_right {

    float: right;
    margin: 41px;
  }

}


#map {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  height: 500px; width: 100%; }



.footer {
  height: 100px;
  background: lavender;
  text-align: center;
  span {line-height: 100px;}
}





.white-popup {
  position: relative;
  background: #FFF;
  padding: 20px;
  width: auto;
  max-width: 500px;
  margin: 20px auto;
}